body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  font-size: 16px;
  background: #64b5f6;
}

.signup-form,
.signin-form {
  padding: 3rem;
  margin: 2rem auto;
  max-width: 500px;
}

.tasks-section {
  margin: 5rem auto;
}

.task-form {
  max-width: 500px;
  margin: 4rem auto;
}

.btn-margin {
  margin-right: 5px;
}

.addTask-section {
  margin: 4rem auto;
  max-width: 600px;
}

.profile-section {
  max-width: 600px;
  margin: 4rem auto;
}